.contact-form{
  margin-top: 20px;
  margin-bottom: 70px;
  height: 400px;
  width: 450px;
  background-color: #46beb8;
  position: absolute;
  transform: translate(-50%,-50%);
  top: 50%;
  left: 50%;
  border-radius: 10px;
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255,255,255,0.1);
  box-shadow: 0 0 40px rgba(8,7,16,0.6);
  padding: 50px 35px;
  

  button{
      border-radius: 50px;
      margin-top: 0px;
      width: 100%;
      background-color: #ffffff;
      color:#2C3E50;
      padding: 10px 0;
      font-size: 18px;
      font-weight: 600;
      height: 40px;
      cursor: pointer;
  }

  button:hover{
    background-color:rgb(247, 247, 247);
  }

}